.single-card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: rgb(23, 121, 23);
  border-radius: 5px;
  border: 1px solid rgb(177, 174, 174);
  padding: 1rem;
  color: white;
  cursor: pointer;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  -webkit-transition: -webkit-transform 2.5 ease-out;
  transition: -webkit-transform 2.5 ease-out;
  -o-transition: transform 2.5 ease-out;
  transition: transform 2.5 ease-out;
  transition: transform 2.5 ease-out, -webkit-transform 2.5 ease-out;
}

.single-card:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}



